import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import App from './App'
import router from './router'
import store from './store'
import '@/icons' // icon
import '@/permission' // 权限
import { Loading } from 'element-ui';

Vue.use(ElementUI)

Vue.config.productionTip = false
Vue.mixin({
    computed: {
        permission() {
            let permission = this.$store.state.permission;
            let obj = {};
            let key = permission.currentRouter;

            if (key &&
                permission.routers_permission[key]) {
                obj = permission.routers_permission[key];
            }

            return obj;
        }
    },
    methods: {
        getMessage(msg, seccess) {
            if (seccess) {
                this.$message({
                    showClose: true,
                    message: msg,
                    type: 'success'
                })
            } else {
                this.$message({
                    showClose: true,
                    message: msg,
                    type: 'error'
                })
            }
        },
        dLoading() {
            Loading.service();
        },
        dLoadingClose() {
            Loading.service().close();
        }
    }
})

new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
})



