import DCTP from './dctp';

let auth = {
    removeSession() {
        return DCTP.session.clear();
    },
    removeLocal() {
        return DCTP.local.clear();
    }
};

['token', 'userId', 'userInfo'].forEach(function (item) {
    let firstWord = item.charAt(0).toUpperCase();
    let key = firstWord + item.substr(1);
    let obj = {};

    obj['set' + key] = val => {
        return DCTP.session.set(item, val);
    };

    obj['get' + key] = () => {
        return DCTP.session.get(item);
    };

    obj['remove' + key] = () => {
        return DCTP.session.clear(item);
    };

    DCTP.utils.assign(auth, obj);
});

export default auth;


