import { getPermissionMenu, getRouterPermission } from '@/api/login'
import { asyncRouterMap, constantRouterMap } from '@/router/index'
import DCTP from '@/utils/dctp';

const _import = require('@/router/_import_' + process.env.NODE_ENV);
const Layout = _import('layout/Layout');
const Empty = _import('layout/Empty');
import fetch from '@/utils/fetch'
function getRouters(data) {
    if (data.children && data.children.length) {
        let routers = [];

        data.children.forEach(item => {
			routers = routers.concat(getOneRouter(item))
        });
        return routers;
    }

    return [];
}

function getOneRouter(data, parentUrl) {
	let routers = [];
	let isDirect = data.url.indexOf('/') !== -1;
	let path = isDirect ? data.url : (parentUrl ? parentUrl + '/' + data.url : data.url);
	path = path.indexOf('/') !== 0 ? '/' + path : path;
	parentUrl = parentUrl || '';

	if (data.children && data.children.length) {
        let children = [];
        let _parentUrl = parentUrl ? parentUrl + '/' + data.url : data.url;

		data.children.forEach(item => {
			children = children.concat(getOneRouter(item, item.url.indexOf('/') === -1 ? _parentUrl : parentUrl));
		});

		routers.push({
			meta: {
				id: data.id,
			},
			path,
			component: parentUrl ? Empty : Layout,
			redirect: 'noredirect',
			name: data.name,
			icon: data.icon || 'el-icon-menu',
			children
		});

	} else {

		routers.push({
			meta: {
				id: data.id,
			},
			path,
			component: parentUrl ? _import(isDirect ? data.url : parentUrl + '/' + data.url) : Layout,
			name: data.name,
			icon: data.icon || 'el-icon-menu'
		});
    }

	return routers;
}

function getPermissionVoList(arr) {
    let list = {};

    if (DCTP.utils.isArray(arr)) {
        arr.forEach(item => {
            list[item.sign] = true;
        })
    }

    return list;
}

var permission_state = {
        routers: constantRouterMap,
        routers_permission: {},
        permission_menu: [],
        addRouters: null,
        currentRouter: ''
    }
if(!sessionStorage["routers_permission"]){
    sessionStorage["routers_permission"] = JSON.stringify({});
}else{
    permission_state["routers_permission"] = JSON.parse(sessionStorage["routers_permission"]);
    permission_state["version"] = new Date();
}

const permission = {
    state :permission_state ,
    mutations: {
        SET_ROUTERS: (state, routers) => {
            state.addRouters = routers
            state.routers = constantRouterMap.concat(routers)
        },
        SET_PERMISSION_MENU(state, menus) {
            state.permission_menu = menus;
        },
        SET_ROUTERS_PERMISSION(state, permissions) {
            DCTP.utils.assign(state.routers_permission, permissions, true, true);
        }
    },
    actions: {
        GenerateRouters({ commit }, data) {
            return new Promise((resolve, reject) => {
                getPermissionMenu().then(response => {
                    if (response.result && response.data) {
                        let data = response.data;
                        let router = getRouters(data)
                        commit('SET_PERMISSION_MENU', [data]);
                        commit('SET_ROUTERS', router);
                        resolve();
                    }
                }).catch(error => {
                    reject(error);
                })
            })
        },
        GenerateRoutersPermission({ commit, state }, router) {
            let id = router.meta && router.meta.id || '';
            let path = router.path || '';

            if (id && path && !state.routers_permission[path]) {

                return new Promise((resolve, reject) => {
                    getRouterPermission(id).then((response) => {
                        if (response.result) {
                            state.routers_permission[path] = getPermissionVoList(response.data.permissionVoList);
                            sessionStorage["routers_permission"] = JSON.stringify(state.routers_permission );
                        }
                        resolve(response);
                    })
                });

            }
        }
    }
}

export default permission



