import DCTP from '@/utils/dctp';

const app = {
  state: {
    sidebar: {
      opened: !+DCTP.local.get('sidebarStatus')
    }
  },
  mutations: {
    TOGGLE_SIDEBAR: state => {
      if (state.sidebar.opened) {
          DCTP.local.set('sidebarStatus', 1)
      } else {
          DCTP.local.set('sidebarStatus', 0)
      }
      state.sidebar.opened = !state.sidebar.opened
    }
    
  },
  actions: {
    ToggleSideBar: ({ commit }) => {
      commit('TOGGLE_SIDEBAR')
    }
  }
}

export default app



