import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 进度条
import 'nprogress/nprogress.css'// Progress 进度条样式
import auth from '@/utils/auth' // 验权

const whiteList = ['/login']
router.beforeEach((to, from, next) => {
  NProgress.start()
  if (auth.getToken()) {
    if (to.path === '/login') {
      next({ path: '/' })
    } else {
      if (store.getters.addRouters === null) {
         store.dispatch('GenerateRouters').then(() => {
             store.getters.addRouters &&
             store.getters.addRouters.length &&
             router.addRoutes(store.getters.addRouters);
             next({ ...to });
         })
      } else {
        next()
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
      NProgress.done()
    }
  }
})

router.afterEach(() => {
    let currentRouter = router.currentRoute;
    let path = currentRouter.path || '';
    store.state.permission.currentRouter = path;
    store.dispatch('GenerateRoutersPermission', currentRouter);
    NProgress.done() // 结束Progress
})



