import fetch from '@/utils/fetch'

export function login(username, password) {
    return fetch({
        url: '/user/login',
        method: 'post',
        data: {
            username,
            password
        }
    })
}

export function getInfo(token) {
    return fetch({
        url: '/user/info',
        method: 'get',
        params: { token }
    })
}

export function logout() {
    return fetch({
        url: 'admin/logout',
        method: 'get'
    })
}

export function getPermissionMenu() {
    return fetch({
        url: 'admin/module/tree',
        method: 'get'
    })
}

export function getRouterPermission(id) {
    return fetch({
        url: '/admin/module/' + id,
        method: 'get'
    })
}


