import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import auth from '@/utils/auth'
import { Loading } from 'element-ui';

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 15000                  // 请求超时时间
})

// request拦截器
service.interceptors.request.use(config => {
  if (store.getters.token) {
    config.headers['token'] = auth.getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
      config.headers['userId'] = auth.getUserId();
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
    return Promise.resolve(response.data)
  },
  error => {
    console.log('err' + error)// for debug
    let response = error.response;

    if (response.config.url.indexOf('admin/logout') === -1 &&
        response.status === 401) {
        let message = auth.getToken() ? '账号在其他电脑已登录，请重新登录' : '请先登录';

        MessageBox.alert(message, '登录', {
            callback: action => {
                if (action === 'confirm') {
                    store.commit('LOG_OUT');
                }
            }
        });
    }
    return Promise.reject(error)
  }
)

export default service



